@charset "UTF-8";
/*Обнуление*/
* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  transition: 0.3s ease all;
}

html {
  scroll-behavior: smooth;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a {
  color: inherit;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit;
}

body {
  font-family: sans-serif;
  font-size: 16px;
  font-weight: 400;
  color: var(--black);
  background-size: cover;
}

.wrapper {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

@media (max-width: 1080px) {
  .wrapper {
    overflow: hidden;
    display: flex;
    flex-direction: column;
    min-height: 100%;
  }
}
@font-face {
  font-family: "Proxima Nova";
  src: url("../fonts/ProximaNova-Light.woff2") format("woff2"), url("../fonts/ProximaNova-Light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Proxima Nova";
  src: url("../fonts/ProximaNova-Regular.woff2") format("woff2"), url("../fonts/ProximaNova-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Proxima Nova";
  src: url("../fonts/ProximaNova-Semibold.woff2") format("woff2"), url("../fonts/ProximaNova-Semibold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Proxima Nova";
  src: url("../fonts/ProximaNova-Bold.woff2") format("woff2"), url("../fonts/ProximaNova-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Proxima Nova";
  src: url("../fonts/ProximaNova-Extrabld.woff2") format("woff2"), url("../fonts/ProximaNova-Extrabld.woff") format("woff");
  font-weight: 800;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Proxima Nova";
  src: url("../fonts/ProximaNova-Black.woff2") format("woff2"), url("../fonts/ProximaNova-Black.woff") format("woff");
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}
:root {
  --proxima: "Proxima Nova";
  --white: #fff;
  --black: #313131;
  --blue: #028A91;
  --blue-2: #13B9C2;
  --blue-3: #73F8F1;
  --green: #124734;
  --green-2: #1D7754;
  --green-3: #86E3A6;
  --yellow: #FF9700;
  --yellow-2: #FFB140;
  --yellow-3: #FFC673;
  --radius: 30px;
}

.title {
  font-family: var(--raleway);
  font-size: var(--title-size);
  line-height: 1;
  font-weight: 700;
  color: var(--white-blue);
}
@media (max-width: 1080px) {
  .title {
    font-size: var(--mobileTitle-size);
  }
}
.title span {
  font-style: italic;
  color: var(--yellow);
  font-weight: 600;
}

.title-big {
  font-size: var(--title-big-size);
  line-height: 1.1;
  font-weight: 500;
}
@media (max-width: 1080px) {
  .title-big {
    font-size: var(--mobileTitle-size);
    line-height: 1.2;
  }
}

.numera {
  color: var(--yellow);
  font-size: var(--numeraSize);
}

.text {
  font-size: var(--textSize);
  line-height: var(--text-line-height);
  color: var(--gray-70-navy);
}
@media (max-width: 1080px) {
  .text {
    font-size: var(--textMobileSize);
    line-height: var(--text-mobile-line-height);
  }
}

.min-text {
  font-size: var(--numeraSize);
  line-height: var(--min-title-line-height);
  color: var(--gray-70-blue-50);
}
@media (max-width: 1080px) {
  .min-text {
    line-height: var(--min-title-mobile-line-height);
  }
}

.tag {
  color: var(--navy-white);
  writing-mode: vertical-rl; /* или vertical-lr */
  padding: 10px 8px;
  background-color: var(--yellow-blue);
  border-radius: 0px 10px 10px 0px;
  position: absolute;
  right: 0px;
  transform: rotate(180deg);
  top: 15%;
}
@media (max-width: 1080px) {
  .tag {
    display: none;
  }
}

.card-title {
  font-size: var(--cardTitleSize);
  line-height: var(--card-title-line-height);
  font-weight: 600;
}
@media (max-width: 1080px) {
  .card-title {
    font-size: var(--cardTitleMobileSize);
  }
}

.button {
  border-radius: var(--radius);
  padding-top: 20px;
  padding-bottom: 20px;
  background-color: var(--blue);
  text-align: center;
}

.mobile-only {
  display: none;
}

.yellow-button {
  background-color: var(--bright-yellow);
  height: 61px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0px 20px;
  color: var(--blue);
  display: inline-flex;
  border-radius: var(--radius);
  font-weight: 500;
  cursor: pointer;
  user-select: none;
}

.yellow-button-big {
  font-size: var(--cardNumSize);
  background-color: var(--bright-yellow);
  height: 120px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  color: var(--navy);
  border-radius: 30px;
  font-weight: 500;
  cursor: pointer;
  user-select: none;
  max-width: 710px;
  width: 100%;
}
@media (max-width: 1080px) {
  .yellow-button-big {
    font-size: var(--textMobileSize);
    height: 50px;
    border-radius: 20px;
    max-width: 231px;
    width: 100%;
  }
}

.button-transparent {
  font-size: var(--textSize);
  color: #fff;
  height: 60px;
  border: 1px solid #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 20px;
  padding: 0px 83px;
}

body.light .only-dark {
  display: none;
}

.only-light {
  display: none;
}

body.light .only-light {
  display: block;
}

@media (max-width: 1080px) {
  .mobile-only {
    display: inherit;
  }
  .desktop-only {
    display: none;
  }
}
.dropdown-simple_list {
  display: none;
}

.header {
  background-color: white;
  border-radius: 40px;
  padding: 20px 7px 20px 20px;
}
.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__logo {
  flex: 0 1 340px;
}
.header__nav {
  flex: 0 1 30%;
  margin-right: auto;
}
.header__list {
  display: flex;
  align-items: center;
  gap: 55px;
}
.header__list_item {
  position: relative;
}
.header__list_item::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 5px;
  height: 5px;
  border-radius: 50%;
}
.header__list_item:nth-child(1)::after {
  background-color: var(--blue-2);
}
.header__list_item:nth-child(2)::after {
  background-color: var(--yellow-2);
}
.header__list_item:nth-child(3)::after {
  background-color: var(--green-3);
}
.header__languages {
  flex: 0 1 13%;
  display: flex;
  align-items: center;
  gap: 10px;
}
.header__languages a {
  transition: 0.3s ease all;
}
.header__languages a:hover {
  color: var(--green-2);
}
.header__languages a:active {
  font-weight: 700;
}
.header__sign {
  border: 1px solid var(--black);
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: var(--radius);
  padding: 0px 40px;
  transition: 0.3s ease all;
}
.header__sign:hover {
  color: var(--white);
  background-color: var(--yellow-2);
  border-color: var(--yellow-2);
}
.header__sign:active {
  background-color: var(--yellow);
}

[class*=__container] {
  max-width: 1420px;
  margin: 0 auto;
  padding: 0px 15px;
  min-width: 0;
}

body {
  color: var(--black);
  font-family: var(--proxima);
}